export default class Form {
	constructor(form) {
		this.forms = document.querySelectorAll(form);
		this.inputs = document.querySelectorAll('input');

		this.message = {
			loading:  "Loading...",
			success: "Thank you! We will contact you shortly.",
			failure:  "Something went wrong..."
		};
	}

	async postData(url, data) {
		let res = await fetch(url, {
			method: "POST",
			body: data
		});
		return await res.text();
	}

	checkMailInputs() {
		const mailInputs = document.querySelectorAll('[type="email"]');
	
		mailInputs.forEach(input => {
			input.addEventListener('keypress', function(e) {
				if (e.key.match(/[^a-z 0-9 @ \.]/ig)) {
					e.preventDefault();
				}
			});
		});
	}

	initMack() {
		let setCursorPosition = (pos, elem) => {
			elem.focus();
			
			if (elem.setSelectionRange) {
				elem.setSelectionRange(pos, pos);
			} else if (elem.createTextRange) {
				let range = elem.createTextRange();
	
				range.collapse(true);
				range.moveEnd('character', pos);
				range.moveStart('character', pos);
				range.select();
			}
		};
	
		function createMask(event) {
			let matrix = '+1 (___) ___-____',
				i = 0,
				def = matrix.replace(/\D/g, ''),
				val = this.value.replace(/\D/g, '');
	
			if (def.length >= val.length) {
				val = def;
			}
	
			this.value = matrix.replace(/./g, function(a) {
				return /[_\d]/.test(a) && i < val.length ? val.charAt(i++) : i >= val.length ? '' : a;
			});
	
			if (event.type === 'blur') {
				if (this.value.length == 2) {
					this.value = '';
				}
			} else {
				setCursorPosition(this.value.length, this);
			}
		}
	
		let inputs = document.querySelectorAll('[name="phone"]');
	
		inputs.forEach(input => {
			input.addEventListener('input', createMask);
			input.addEventListener('focus', createMask);
			input.addEventListener('blur', createMask);
		});
	}

	init() {
		this.initMack();
		this.checkMailInputs();
		this.forms.forEach(form => {
			form.addEventListener('submit', (e) => {
				e.preventDefault();
				let data = new FormData(form);

				const messageStatus = document.createElement('div');
				messageStatus.style.cssText = `
					margin-top: 15px;
					font-size: 18px;
					color: grey;
				`;
				form.parentNode.appendChild(messageStatus);
				messageStatus.textContent = this.message.loading;


				this.postData('assets/question.php', data)
					.then(res => {
						messageStatus.textContent = this.message.success;
						console.log(res);
					})
					.catch(() => {
						messageStatus.textContent = this.message.failure;
					})
					.finally(() => {
						setTimeout(() => {
							messageStatus.remove();
							this.inputs.forEach(input => {
								input.value = '';
							});
						}, 5000);
					});
			});
		});

		
		
	}
}